          .tarjeta.tarjeta--amarilla--borde.p-4(titulo="3: cdigo")
            h4 Cdigo
            p.mt-4 Lenguaje, signos o smbolos que el emisor utiliza para transmitir el mensaje. El idioma es el cdigo ms utilizado. El lenguaje como cdigo en la comunicacin puede ser lingstico, el cual se puede presentar verbal o escrito. Verbal en un mensaje por radio y escrito el mensaje presentado en una valla publicitaria.
          .tarjeta.tarjeta--amarilla--borde.p-4(titulo="4: el cdigo no lingstico")
            h4 El cdigo no lingstico
            p.mt-4 Visual a travs de smbolos o signos. Ejemplo las seales de trnsito o la tarjeta amarilla que le saca un rbitro a un jugador. El mensaje no lingstico puede ser gestual o con movimientos del cuerpo (La forma de comunicarse los sordomudos). Cdigo no lingstico auditivo, a travs de sonidos. La campana del carro recolector de basuras. El pito de un rbitro. Las empresas emplean este cdigo cuando tratan de ensearle al consumidor a reconocer un producto con solo escuchar un sonido musical, ejemplo: coordinadora mercantil.
          .tarjeta.tarjeta--amarilla--borde.p-4(titulo="5: mensaje")
            h4 Mensaje
            p.mt-4 Es el contenido de la informacin (ideas, sentimientos o acontecimientos) que desea transmitir el emisor y que desea que sea captado como l quiere. Para lograr esa captacin, es importante que el emisor emplee el cdigo y el medio correcto a fin de que el mensaje no sea interpretado de manera diferente a lo que se quiere.
          .tarjeta.tarjeta--amarilla--borde.p-4(titulo="6: canal")
            h4 Canal
            p.mt-4 Es el medio a travs de los cuales se transmite el mensaje (televisin-radio-telfono, internet, una seal de desvo en una carretera).
          .tarjeta.tarjeta--amarilla--borde.p-4(titulo="7: respuesta")
            h4 Respuesta
            p.mt-4 Cuando el receptor ha captado el mensaje en la forma que se espera (comprensin del mensaje). En el marketing, cuando el consumidor recuerda el producto o lo compra.    
