              p Una organizacin fuertemente orientada hacia el mercado, que lo haya segmentado y que realice un seguimiento permanente del comportamiento de los clientes, se encuentra en la mejor posicin para construir, de forma exitosa, la identidad de la marca. Una empresa centrada en su interior, sencillamente no contar con la informacin suficiente del mercado para construir una identidad de marca significativa para su pblico objetivo. A la hora de desarrollar la identidad de la marca, el primer paso es definir el posicionamiento deseado para el producto y la proposicin de valor para un pblico objetivo determinado #[strong (Best, 2007).]
    p.mt-5 Para construir una marca poderosa o una marca fuerte #[strong Hoyos (2016)], determina que se debe propender por construir y fortalecer su Brand equity, esto significa tener una marca conocida por muchas personas, asociada a elementos positivos, con una calidad percibida alta, con una base de consumidores numerosa y fidelizada, adems, con conexiones emocionales profundas.
