    figcaption Referencia Adaptada de Hoyos (2016)
    p.mt-5 En la figura anterior, se muestra que el proceso de construccin del #[strong Brand equity] comienza por seleccionar un producto y un mercado especfico, posteriormente, se procede a definir la identidad de marca, la manera cmo quiere ser percibida; el tercer paso, consiste en escoger un nombre de marca y algunos smbolos identificadores normalmente solo el logosmbolo y los colores marcarios; el cuarto paso, la construccin del #[strong Brand equity], con la dinmica de la organizacin con su mercado en la acumulacin de marca.
    p.mt-4 Los consumidores asocian las marcas a diferentes elementos, dependiendo de su experiencia y de su relacin con otros usuarios de estas, adems, de acuerdo con el modelamiento que hacen los medios de comunicacin a travs de contenidos editoriales y de contenidos comerciales; es decir, la publicidad pagada por las marcas. Las redes sociales estn jugando un papel muy importante en la construccin de asociaciones positivas y negativas de las marcas gracias al intercambio permanente de comentarios entre los usuarios de estas con respecto a sus experiencias de consumo #[strong (Hoyos, 2016).]
