        p #[strong Hoyos (2016, citando a Aaker y Joachimsthaler, 2006)] explica que para construir la identidad de marca es necesario comenzar con un anlisis estratgico, en donde se analizan los clientes, la competencia y se realiza un autoanlisis. Luego, se establecen los puntos relevantes del producto como su alcance, atributos, relacin calidad/valor, usos, usuarios y pas de origen; posteriormente, se revisan aspectos relativos a la organizacin, que puedan ayudar a construir la identidad de la marca, entre los que estn los atributos organizativos y la preocupacin de la empresa por sus clientes; anteriormente, se establecen cules son aquellos elementos de la marca asociados a caractersticas de personalidad y se desarrolla lo que se conoce como el personaje de la marca.
