    p.mt-4 La marca como producto, la marca como empresa, la marca como persona y la marca como smbolo, se utilizan para alimentar la identidad compuesta por la esencia de la marca, lo que algunos califican como el alma o el ADN de esta #[strong (Toulemonde, 2012).] 
