    .row.mt-5
      .col-12.col-lg-7.tarjeta.tarjeta--naranja.py-5.px-4
        p.mb-0 #[strong OGuinn (2013)], define las audiencias como un grupo de individuos que recibe e interpreta los mensajes enviados por las empresas u organizaciones. La audiencia podra estar conformada por hogares consumidores, estudiantes universitarios o personas de negocios; adems, cualquier grupo grande de personas puede ser una audiencia. La audiencia meta es un grupo particular de consumidores destacados por una organizacin para una publicidad o una campaa de PIM. Estas audiencias meta se destacan porque la empresa ha descubierto que a los miembros de la audiencia les agrada la categora de producto y podran preferir su marca en particular dentro de esa categora de producto. Las audiencias meta, siempre son audiencias potenciales porque una empresa nunca puede estar segura de que el mensaje en realidad les llegar como ellos pretendieron.
      .col-4.d-none.d-lg-block.align-self-center.offset-1
        figure.mb-5
          img(src="@/assets/template/tema-1-18.svg", alt="Texto que describa la imagen").w-75
    p.mt-5 El pblico podra componerse de usuarios actuales o compradores potenciales, aquellos que toman la decisin de compra o que influyen en ella. El pblico puede estar integrado por individuos, grupos, audiencias especiales o pblico en general. El pblico meta afectar de manera importante las decisiones del comunicador sobre lo que se dir, cmo, cundo, dnde y quin lo dir #[strong (Kotler, 2017).]
    .titulo-segundo.mt-5
      #t_1_5.h4 1.5.  Estrategia de comunicacin
    .row.mt-4
      .col-12.col-lg-5.align-self-center
        p Las estrategias de comunicacin tienen diversas clasificaciones en la variable del marketing mix, es necesario conocerlas para definir el mejor camino a seguir en la comunicacin de marketing. 
        p.mt-4 La estrategia de comunicacin PULL la integran el conjunto de acciones de comunicacin de marketing dirigidas a los usuarios finales. Su objetivo, es construir notoriedad, inters y lealtad en los clientes finales. Cuando esta estrategia se ejecuta con xito, los usuarios finales buscan y demandan determinados productos y servicios, y en esencia, su inters consigue atraer el producto hacia el canal. El final exitoso en la ejecucin de esta estrategia requiere que los intermediarios dispongan de los productos y marcas buscados y demandados por los usuarios finales #[strong (Best, 2007)].
      .col-7.d-none.d-lg-block.align-self-center
        figure.mb-2
          img(src="@/assets/template/tema-1-19.png", alt="Texto que describa la imagen")
      p.mt-4 La estrategia de comunicacin PUSH la integran el conjunto de acciones de comunicacin de #[strong marketing] dirigidas a los intermediarios. Su objetivo es motivar a los intermediarios para que pongan a disposicin de los usuarios finales, determinados productos o marcas, y, de esta forma, mejore su disponibilidad de los productos promocionados. Cuando estas estrategias se ejecutan con xito mejora la disponibilidad del producto, los fuera de stock, el marketing en el punto de venta #[strong (merchandising)], y el esfuerzo de marketing del intermediario hacia los productos que se han promocionado a travs de esta comunicacin PUSH. Sin embargo, es importante llegar a comprender que la mejor forma de conseguir una respuesta positiva de compra, y por lo tanto mejoras de cuota de mercado, es a travs de la utilizacin conjunta de las estrategias PUSH y PULL #[strong (Best, 2007)], como se muestra en la siguiente figura.
    .row
      .col-10.offset-1
        figure.mt-5
          img(src="@/assets/template/tema-1-20.png", alt="Texto que describa la imagen")
        figcaption.mt-3 Referencia Adaptada de Best (2007)
    .row.mt-5
      .col-12.col-lg-6.align-self-center
        p La estrategia creativa incluye todo aquello que ayuda a definir mejor el mensaje, como la idea creativa, el concepto, un posible eslogan de la marca/empresa, el denominado #[strong Reason why] (justificacin de la promesa o beneficio del producto/servicio que ofrece la empresa), el tono del mensaje, el eje de la comunicacin (la idea central de la comunicacin/campaa) y el elemento motivador de la misma (si lo hubiera).   
        p.mt-4 El sentido general del mensaje se basar en que el cliente conozca la ventaja del servicio que se ofrece, es decir, la diferenciacin conseguida a travs de la exclusividad en el servicio, la posibilidad de personalizacin y el sentido que tiene el adquirir algo nico #[strong (Estrella, 2018)]. 
      .col-6.d-none.d-lg-block.align-self-center.align-self-center
        figure.mb-2
          img(src="@/assets/template/tema-1-21.svg", alt="Texto que describa la imagen").w-75
    
    AcordionA.mt-5(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Estrategias ATL")
        .col-3.d-none.d-md-block.align-self-center.offset-1
          figure.mb-2
            img(src="@/assets/template/tema-1-22.svg", alt="Texto que describa la imagen").w-75
        .col-12.col-md-8
          p La estrategia publicitaria ATL, Above The Line, o publicidad sobre la lnea es el tipo de publicidad que utiliza medios publicitarios convencionales.
          p.mt-4 El ATL, al hacer uso de los medios tradicionales masivos, lo que comunica en un mensaje tendr un gran alcance de audiencia, incluso a quienes la marca no considere su target. Esto obliga a las estrategias ATL de las marcas a comunicar un mensaje que se ajuste a la comprensin de todo el pblico que se exponga a la comunicacin, y a estar consciente de las medidas de respeto y cautela necesarias al dirigirse al pblico #[strong (Torreblanca, 2016)].
          p.mt-4 Segn #[strong Chong (2007)], los servicios de marketing ATL y BTL representan dos puntos de vista diferentes en cuanto a la recordacin de marca y la estrategia del consumidor objetivo. ATL es sinnimo de marketing masivo y de grandes campaas publicitarias, mientras que BTL est basado en informacin muy medible y enfocada en los patrones de compra del consumidor y los programas de retencin.

      .row(titulo="Estrategias BTL")
        .col-3.d-none.d-md-block.align-self-center.offset-1
          figure.mb-2
            img(src="@/assets/template/tema-1-23.svg", alt="Texto que describa la imagen").w-75
        .col-12.col-md-8
          p Otra forma de hacer publicidad por parte de las empresas es a travs de los BTL (de Below The Line). Literalmente esto traducira por debajo de la lnea, es decir, fuera de las lneas tradicionales utilizadas para hacer publicidad. La publicidad BTL resulta menos costosa, y se puede direccionar a segmentos puntuales, hacindose por esta razn, ms efectiva por el logro del mejor contacto con los posibles clientes. BTL son diferentes estrategias en que no se utilizan los medios de comunicacin tradicionales y estas actividades facilitan el contacto directo con el cliente; son por lo tanto mayormente motivadoras para los consumidores #[strong (Prettel, 2016)].
          p.mt-4 Entre las estrategias de BTL ms conocidas se pueden citar: los eventos en lugares pblicos (supermercados, parques, universidades), los festivales y las estrategias que hacen parte del marketing directo. De igual manera, al implementar un programa publicitario, se debe interpretar ciertas conductas del consumidor, para lo cual es necesario tener en cuenta tres aspectos: los estmulos, las respuestas, y ciertas reacciones intermedias entre el estmulo y las respuestas. Por otra parte, es necesario tener en cuenta que la respuesta al mensaje, se da de acuerdo con las caractersticas o perfil del segmento metas y el tipo de producto que se anuncia #[strong (Prettel, 2016)].
    .titulo-segundo.mt-5
      #t_1_6.h4 1.6.  Promocin
    .row.mt-5
      .col-12.col-lg-7.align-self-center
        p La promocin de ventas es una estrategia de comunicacin con la que se estimula de manera directa al consumidor a travs de beneficios adicionales, como regalos, descuentos, rifas, bonos, y con lo que se espera mayor incidencia sobre el resultado final de las ventas y a corto plazo. Quien define un programa de promocin espera una pronta respuesta por parte del consumidor. Por tal razn son a corto plazo, no generando en muchos casos lealtad en el mercado. El resultado deber ser un incremento en los niveles de ventas #[strong (Prettel, 2016)].   
        p.mt-4  Con base en #[strong Prettel (2016)], se describen las condiciones fundamentales que debe tener un programa de promocin.
      .col-5.d-none.d-lg-block.align-self-center.align-self-center
        figure.mb-2
          img(src="@/assets/template/tema-1-24.svg", alt="Texto que describa la imagen")
    .row.mt-5
      .col-10.offset-1.rounded.borde-gris.p-5
        .row
          h5 Objetivos promocionales
          p.mt-3 El programa de promocin de ventas, como todos los de la mezcla de mercadeo, debe articularse con los objetivos de la empresa. Algunos objetivos pueden estn enfocados a informar, estimular la demanda, evitar la desercin de clientes, cambio de actitud frente al producto, estmulo para los distribuidores, competir, generar ingresos y generar crecimiento corporativo.
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-5
        .row
          h5 Tipo de producto
          p.mt-3 De acuerdo con el producto que se promociona, se establece el mtodo ms conveniente. El tipo de producto tambin influye en la escogencia de los lugares de promocin.
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-5
        .row
          h5 Segmento meta
          p.mt-3 Los consumidores reaccionan o asumen una actitud frente a un determinado bien o servicio, segn el segmento al que pertenecen.
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-5
        .row
          h5 Competencia
          p.mt-3 Desconocer lo que estn haciendo el resto de oferentes que se disputan el mercado puede ocasionar errores que son aprovechados por ellos mismos.
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-5
        .row
          h5 Ciclo de vida del producto
          p.mt-3 La etapa de introduccin deber caracterizarse por una campaa promocional intensa paralela a la publicitaria, para que, se logren superar los obstculos propios de esta etapa como el desconocimiento del producto por parte de los consumidores, resistencia al cambio o lealtad hacia las marcas ya establecidas y los ataques de la competencia. En la etapa de crecimiento disminuye la intensidad de la promocin de ventas, ya que se espera que el mercado tenga mayor conocimiento del producto. En la etapa de madurez son frecuentes las promociones, debido a que en esta etapa se han incrementado significativamente el nmero de competidores, lo que hace que los consumidores tengan muchas opciones de productos para satisfacer sus necesidades. La promocin en este caso busca incrementar las ventas y mantener la lealtad a una marca.
    h5.mt-5 Mtodos de promocin
    p.mt-2 A continuacin, se describen los mtodos de promocin.
    .row.mt-5
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-25.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 La degustacin
            p.mt-3 Se puede realizar en el punto de venta o en cualquier tipo de mercado. Al consumidor se le da a que experimente de manera inmediata el producto, logrando en el mismo momento una opinin al respecto. Mtodo muy utilizado para jugos, galletas, margarinas, carnes fras, entre otros, siendo de gran estmulo para la compra. Un lugar comnmente utilizado para este tipo de promocin son los supermercados de cadena, en los que se aprovecha al grupo de promotoras para impulsar en el punto de venta, que debe estar ubicado en un lugar estratgico, #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-26.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 Muestra gratis  
            p.mt-3 Se usa con frecuencia consiste en ofrecer el producto gratis o a un precio muy reducido. A menudo se usan con nuevos productos, ya que las muestras ponen el artculo en manos de los consumidores (Kerin, 2018). Un mtodo que se puede aplicar en desodorantes, champ para el cabello, jabones, entre otros. Para este caso, se debe ser cuidadoso en la definicin de la unidad que se va a promover, la cual, se aconseja, deber ser de un tamao menor que el de la unidad de venta, a fin de no incrementar los costos de la campaa. Lo que se busca es que el consumidor que recibe la muestra (que hasta ese momento es un comprador en potencia), despus de experimentarlo, pueda llegar a identificarse con la calidad del producto, convirtindose en un comprador real; desertando de la competencia #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-27.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 Demostraciones
            p.mt-3 De bastante aplicacin por la fuerza de ventas cuando se trata de estimular la compra de un equipo (computadora, mquina de escribir, equipo de sonido, muebles especiales, entre otros) y ante un prospecto de cliente. El vendedor lleva consigo un equipo que instala y pone a funcionar de inmediato e instruye sobre su manejo y precauciones. Si es del caso lo deja durante un perodo para que el posible cliente se familiarice con su manejo y logre encontrar en l las bondades que satisfagan sus necesidades #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-28.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5  Descuentos
            p.mt-3 Las ofertas o descuentos son reducciones del precio a corto plazo, que en general se usan para incrementar la prueba entre posibles clientes o tomar represalias contra las medidas adoptadas por un competidor, #[strong (Kerin, 2018)]. Para almacenes que comercializan productos, es un buen mtodo para reactivar las ventas y generar ingresos importantes; tambin estos programas son de gran beneficio en poca de recesin o en aquellos ciclos ya tratados donde la demanda de ciertos productos baja. Para los meses de febrero a marzo, los consumidores despus de pasar la temporada navidea de grandes gastos, disminuyen su nivel de consumo de ropa, calzado, cosmticos; y de la nica manera que se motivan a generar otros gastos es cuando son atrados por los llamativos descuentos. Las mismas compaas productoras apoyan a sus distribuidores en este tipo de campaas, logrando beneficio mutuo #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-29.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5  Programas especiales de crdito
            p.mt-3 Cuando una empresa comercializadora emplea el llamado plan prima (lleve su equipo ahora y pague la cuota inicial con su prima de Navidad), est estimulando la compra de sus mercancas, siendo esta otra forma de promocin. Estos mtodos se convierten en factor competitivo muy utilizado por comercializadoras de electrodomsticos y en general artculos para el hogar. Dentro de este mismo programa se emplea la estrategia de recibir el equipo o vehculo viejo como parte de pago #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-30.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 Programas de lealtad
            p.mt-3 Se usan para estimular y premiar las adquisiciones recurrentes al distinguir cada compra realizada por un consumidor y ofrecer una prima a medida que las compras se acumulan. Los programas de continuidad ms populares en la actualidad son los programas de recompensas de las tarjetas de crdito. Ms de 75% de las tarjetas ofrecen incentivos por usarlas #[strong (Kerin, 2018)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-31.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 Las empresas de servicios
            p.mt-3 Hoteles, agencias de viajes, programas vacacionales, compiten igualmente con descuentos por pago inmediato, y crditos con amplios plazos y bajas tasas de inters #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-32.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5  Regalos, premios, sorteos
            p.mt-3 Son mtodos de gran impulso en la actualidad, con los cuales los almacenes de cadena y empresas en general tratan de atraer clientes (Prettel, 2016). El premio es una herramienta promocional que se usa a menudo con los consumidores; consiste en mercanca gratuita o con ahorros significativos sobre su precio  de venta al menudeo. Este ltimo tipo de premio se llama de autoliquidacin porque el costo que se cobra al consumidor cubre el costo del artculo. Los concursos o sorteos consisten en que los consumidores aplican sus habilidades o pensamiento analtico o creativo para competir por un premio. Esta forma de promocin ha crecido a medida que las solicitudes de videos, fotos y ensayos pueden coincidir con la tendencia al contenido generado por los consumidores en internet #[strong (Kerin, 2018)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-33.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5 Cupones
            p.mt-3 Son promociones de ventas que por lo general ofrecen un precio de descuento al consumidor, lo que anima la prueba (Kerin, 2018). Cuando el cliente presenta el respectivo cupn recibir a cambio un descuento especial #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
        .row.py-4
          .col-3.d-none.d-lg-block.align-self-center
            figure.mb-2(style='margin-left: -30px')
              img(src="@/assets/template/tema-1-34.svg", alt="Texto que describa la imagen")
          .col-12.col-lg-9.align-self-center  
            h5  Reembolsos
            p.mt-3 Otra promocin de ventas para los consumidores es el reembolso de efectivo, el cual ofrece la devolucin de dinero con base en el comprobante de compra (Kerin, 2018). Por ejemplo, Carrefour manej una promocin en la que, pasado un perodo despus de haber hecho la compra, el cliente puede regresar a retirar el valor del IVA (impuesto al valor agregado) #[strong (Prettel, 2016)].
    .row
      .col-10.offset-1.borde-top-gris
    .titulo-segundo.mt-5
        #t_1_7.h4 1.7. Publicidad
    .row
      .col-12
        figure.mb-2
          img(src="@/assets/template/tema-1-35.png", alt="Texto que describa la imagen")
        p.mt-5 La publicidad es cualquier forma de comunicacin no personal sobre una organizacin, producto, servicio o idea, pagada por un patrocinador identificado. El aspecto del pago en esta definicin es importante porque el espacio para el mensaje publicitario casi siempre tiene que comprarse. Una rara excepcin es el anuncio de servicio pblico, donde el tiempo o espacio para el anuncio se dona. El componente impersonal de la publicidad tambin es importante. La publicidad tiene que ver con los medios masivos de informacin (televisin, radio, revistas e internet), que no son personales y no tienen un ciclo de retroalimentacin inmediato #[strong (Kerin, 2018)].
      .col-10.offset-1
        figure.mb-2.mt-5
          img(src="@/assets/template/tema-1-36.png", alt="Texto que describa la imagen")
    .row.mt-5
      p Se mencionan a continuacin, los principales objetivos de un programa publicitario de acuerdo con #[strong Prettel (2016)]:
    .row.mt-5
      .col-sm.mb-5.mb-sm-0
        ul.lista-ul
          li.mb-0 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Informar sobre la existencia de un nuevo producto o servicio:] hacerle saber al mercado su existencia para motivar al segmento meta a que compre el producto. 
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Persuadir: ] debe convencer o generarle seguridad al consumidor sobre los beneficios que encontrar en el producto anunciado.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Contribuir al posicionamiento de la marca:] los consumidores ya han experimentado el producto, ahora se debe insistir para que el consumidor relacione la marca con sus beneficios.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Contribuye a crear lealtad a la marca:] romper hbitos, adquirir hbitos y reforzar hbitos.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Recordar el producto a los consumidores:] con el fin de evitar el olvido del producto por parte del consumidor, lo que desestimula el consumo, se desarrollan programas publicitarios de recordacin a travs de diferentes medios.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Aumentar el valor del producto en la mente de los consumidores:] los productos no es que aumenten su calidad por generar ms anuncios. Pero lo que s sucede es que los productos anunciados generan en el consumidor la percepcin de que son de mejor calidad y que generan mejores beneficios.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Estimular el consumo del producto:] el consumidor compra del producto despus de recibir el estmulo.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Cambiar la actitud negativa de los consumidores respecto al bien o servicio: ] algunos consumidores, asumen actitud negativa frente a un producto la comunicacin publicitaria puede ayudar al cambio de actitud.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Anunciar el mejoramiento de un producto o servicio (innovacin): ] se anuncian sus nuevos beneficios.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Anunciar una promocin de ventas: ] creando expectativa entre la gente, a partir del momento del primer anuncio quedan pendientes de la fecha de inicio, para acudir masivamente.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Servir de factor competitivo: ] la publicidad hace ms fuertes a las grandes empresas. Los altos presupuestos destinados a publicidad, hacen que las pequeas empresas queden al margen en la disputa de grandes y lucrativos mercados.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Crear una imagen favorable de la compaa:] es utilizada para resaltar la imagen corporativa, siendo de alguna manera un complemento a las relaciones pblicas que pretenden recordar permanentemente la categora lograda por la organizacin.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Realizar marketing social: ] los medios de comunicacin son utilizados para transmitir mensajes que buscan fomentar el bienestar entre la comunidad con programas de marketing social. Mensajes por la paz, por la solidaridad, campaas contra el consumo de drogas, son comunes.
    .row.mt-5
      .col-12.col-lg-7.align-self-center
        p #[strong OGuinn (2013)], explica que la publicidad significa diferentes cosas para distintas personas. Es un negocio, una forma de arte, una institucin y un fenmeno cultural. Para el #[strong CEO], la publicidad es una herramienta esencial de marketing que ayuda a crear conciencia de marca y lealtad a la misma. Para el dueo de una pequea tienda minorista, la publicidad es una forma de llevar gente a la tienda. Para el director de arte en una agencia de publicidad, la publicidad es la expresin creativa de un concepto. Para un planificador de medios, la publicidad es la forma en que una empresa utiliza los medios para comunicarse con los clientes actuales y potenciales. Para el gerente de un sitio web, es una forma de dirigir trfico hacia la URL. Para acadmicos y curadores de museos, la publicidad es un artefacto cultural importante, un texto, as como un registro histrico. As las cosas, la publicidad significa algo diferente para todas estas personas.
      .col-5.d-none.d-lg-block.align-self-center.justify-content-center
        figure.mb-2.mt-5
          img(src="@/assets/template/tema-1-37.svg", alt="Texto que describa la imagen").w-75

    .titulo-segundo.mt-5
      #t_1_8.h4 1.8.  Relaciones pblicas
    figure.mt-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/aOwMfkV_1H4" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    p.mt-5 Las relaciones pblicas son el conjunto de actividades para atraer a los clientes y establecer buenas relaciones con los diversos pblicos de una compaa. Se utilizan para promover productos, personas, lugares, ideas, actividades, organizaciones e incluso pases. Las compaas utilizan las relaciones pblicas para entablar buenas relaciones con los consumidores, inversionistas, medios de comunicacin y con sus comunidades #[strong (Kotler, 2017)].
    p.mt-4 Las relaciones pblicas constituyen una forma de administracin de la comunicacin que trata de influir en los sentimientos, opiniones o creencias de los clientes, posibles clientes, accionistas, proveedores, empleados y otros pblicos acerca de una empresa y sus productos o servicios. Muchas herramientas, como los eventos especiales, informes anuales, conferencias de prensa y manejo de imagen, pueden ser utilizadas por un departamento de relaciones pblicas #[strong (Kerin, 2018)].
    figure.mb-2.mt-5
      img(src="@/assets/template/tema-1-38.png", alt="Texto que describa la imagen")
    p.mt-5 #[strong Prettel (2016)], define los principales mtodos para hacer relaciones pblicas, a saber:
    .row.mt-3
      .col-sm.mb-5.mb-sm-0
        ul.lista-ul
          li.mb-0 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Patrocinio de eventos.] Las compaas los usan para promocionar su negocio o servicio.  
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Patrocinio de congresos.] Algunos laboratorios apoyan la realizacin de congresos como el odontolgico, el peditrico, aprovechando estos eventos para impulsar sus productos entre los profesionales asistentes.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Donaciones.] Las donaciones hechas a hospitales, instituciones de educacin, construccin de clubes para la recreacin popular, como lo hace la compaa Colgate-Palmolive son, as mismo, relaciones pblicas que les permite mantener una gran imagen institucional entre toda la comunidad.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Almuerzos para ejecutivos.] Grandes compaas realizan peridicamente almuerzos, a los cuales son invitados ejecutivos pertenecientes a sectores industriales, comerciales o de servicios. Ejemplo: industria del calzado, sector hotelero, entre otros.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Regalos.] En ciertas pocas del ao, como en Navidad, se acostumbra obsequiar regalos consistentes en agendas de gran utilidad en el trabajo, adornos para el escritorio, almanaques decorativos con el nombre de la compaa que lo enva, bolgrafos, encendedores, entre otros.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Material escrito.] Pueden ser artculos escritos para revista o prensa, catlogos o folletos que se les envan a los clientes.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Material audiovisual.] Presentado en teatros o emisoras que comunican acerca de nuevos productos o eventos de la compaa.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p #[strong Entrevistas con la prensa o noticieros de televisin o radio.] En los cuales se les suministra alguna noticia sobre algn avance cientfico que tenga relacin con un producto o con la misma compaa. Esto conduce a la buena propaganda.
    .titulo-segundo.mt-5
      #t_1_9.h4 1.9. Canales de comunicacin
    .row.mt-4
      .col-12.col-md-6.align-self-center  
        p Estrella (2018), enfatiza que, en el entorno actual, multimedia y multicanal, es necesario aprender a manejar cada canal y las opciones de comunicacin que estos les brindan para maximizar las ventas y el valor de la marca con cada una de las opciones comunicativas, el reto reside en elegir entre ellos bajo una perspectiva coordinada e integrada los tipos de canales de comunicacin que maximicen las estrategias push y pull (empujar y tirar). A continuacin, se explican los tipos de canales.
      .col-6.d-none.d-md-block 
        figure.mb-2.mt-5
          img(src="@/assets/template/tema-1-39.png", alt="Texto que describa la imagen")
    
    TabsB.mt-5
      .py-4.py-md-5(titulo="Canales directos e interactivos" :icono="require('@/assets/template/tema-1-44.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Permiten a la empresa comunicar sus mensajes a travs de sus contactos personales a clientes potenciales haciendo uso del correo, telfono, Internet, mvil, visitas personales, etc. Se trata de una opcin relacionada con la estrategia push (empujar), al orientarse los esfuerzos de comunicacin sobre un intermediario (en este caso los contactos personales de la propia empresa).
          .col-md-6.justify-content-center
            figure
              img(src='@/assets/template/tema-1-40.svg', alt='Texto que describa la imagen').w-50
      .py-4.py-md-5(titulo="Canales Indirectos" :icono="require('@/assets/template/tema-1-45.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Permiten a la empresa comunicarse a travs de intermediarios, como agentes o brokers, distribuidores, etc. Se trata de nuevo de una opcin relacionada con la estrategia push (empujar).
          .col-md-6.justify-content-center
            figure
              img(src='@/assets/template/tema-1-41.svg', alt='Texto que describa la imagen').w-50
      .py-4.py-md-5(titulo="Comunicaciones Personales" :icono="require('@/assets/template/tema-1-46.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Permiten a la empresa poner en prctica el concepto de comunicacin uno a uno entre un gestor de marketing o representante de la empresa y un consumidor a travs de venta personal, marketing directo, marketing online, la generacin del word-of-mouth, etc. Se trata de una opcin relacionada con la estrategia pull (tirar), por ser la propia empresa la que comunica.
          .col-md-6.justify-content-center
            figure
              img(src='@/assets/template/tema-1-42.svg', alt='Texto que describa la imagen').w-50

      .py-4.py-md-5(titulo="Comunicaciones Masivas" :icono="require('@/assets/template/tema-1-47.svg')")
        .row
          .col-md-6.mb-4.mb-md-0
            p Permiten a la empresa comunicar a un grupo de clientes a travs de la publicidad, marketing promocional, eventos y experiencias, etc. De nuevo se trata de una opcin relacionada con la estrategia pull (tirar).
          .col-md-6.justify-content-center.align-self-center
            figure
              img(src='@/assets/template/tema-1-43.svg', alt='Texto que describa la imagen').w-50
    p.mt-5 Entre los medios de comunicacin utilizados tradicionalmente para transmitir mensajes publicitarios se pueden mencionar: televisin, radio, internet, cine, prensa, revistas, vallas, pendones, volantes, megfonos, catlogos, folletos, vehculos de transporte, los telfonos celulares, y los BTL, entre otros #[strong (Prettel, 2016)].
    .row.mt-5
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-48.svg", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Televisin
            p.mt-4 La televisin es un medio valioso porque comunica con la vista, el sonido y el movimiento. Los anuncios impresos por s solos jams podran darle la sensacin de un carro deportivo acelerando desde cero o tomando una curva a gran velocidad. Tambin existen muchas oportunidades para ver la televisin fuera de casa, en lugares como bares, hoteles, oficinas, aeropuertos y campus universitarios #[strong (Kerin, 2018)].
            p.mt-4 Entre las ventajas de la televisin se encuentran captura de audiencias masivas, la combinacin de imagen, movimiento, sonido, colores, facilita la creatividad en el desarrollo del mensaje, una persuasin efectiva, gracias a la utilizacin de testimonios y a las demostraciones de las caractersticas de un producto o servicio, permite llevar el mensaje a audiencias diferentes de acuerdo con horarios o franjas diferentes, logra de manera ms rpida el Estmulo / Respuesta. La principal desventaja es el alto costo, el pago al testimonio a utilizar que en ocasiones es un personaje famoso, el pago de honorarios al publicista creativo, camargrafos, entre otros, as como la compra de tiempo al canal, requieren del aporte por parte de la empresa de una gran capital #[strong (Prettel, 2016)].
            p.mt-4 Como el 70% del conocimiento es visual, el 20% es auditivo, el 10 % del resto de los sentidos y el costo por millar de espectadores es bajo, esta tiene grandes posibilidades de transmitir mensajes de gran impacto #[strong (Prieto, 2018)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-49.svg", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Radio
            p.mt-4 La radio es un medio masivo de comunicacin, se escucha en cualquier parte, lo que facilita llegar a todas las audiencias.
            p.mt-4 Las ventajas de la radio son lograr gran cobertura, la tecnologa le permite llegar a cualquier lugar y ser escuchada por cualquier audiencia con algunas diferencias de acuerdo con ciertas caractersticas de la poblacin logrndose la mayor audiencia en los sectores de bajos ingresos #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-50.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Medios impresos
            p.mt-4 Deben ser utilizados para anuncios de productos o servicios con alto nivel de involucramiento por parte del consumidor. Los medios escritos permiten la comunicacin amplia acerca de las bondades o beneficios de un bien o servicio. 
            p.mt-4 Las ventajas de las revistas y peridicos como medio impreso es que son de gran circulacin y pueden estar al alcance de todos, en salas de espera, consultorios mdicos, la recepcin de cualquier empresa. Todo esto aumenta su circulacin y por derivacin el gran nmero de lectores #[strong (Prettel, 2016)].
            p.mt-4 Los peridicos constituyen un medio local importante con un gran potencial de alcance. Debido a que en su mayora los peridicos se publican de manera cotidiana, permiten que los anuncios se centren en acontecimientos especficos de actualidad, como una venta de 24 horas. Adems, los comerciantes minoristas locales suelen usar los peridicos como medio publicitario exclusivo #[strong (Kerin, 2018)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-51.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Internet
            p.mt-4 Cada da aumenta el nmero de personas que tienen acceso a las pginas web y redes sociales, incrementando por ello la difusin de los productos y las posibilidades de comercializarlos por parte de las empresas. Son varias las formas de anunciar por internet: sitios web, banners o pequeos anuncios dentro de la pgina #[strong (Prettel, 2016)].
            p.mt-4 Internet ha atrado una amplia variedad de industrias, as la publicidad en lnea se parece a la publicidad impresa que ofrece un mensaje visual. Sin embargo, tiene ventajas adicionales porque tambin usa las capacidades de audio y video de internet. El sonido y el movimiento pueden atraer ms la atencin de los espectadores o proporcionar un elemento de entretenimiento al mensaje. La publicidad por internet tambin tiene la caracterstica nica de ser interactiva #[strong (Kerin, 2018)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-52.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Vallas
            p.mt-4 Estn ubicadas preferiblemente en carreteras y en la entrada de las grandes ciudades. Las vallas publicitarias colocadas dentro de la ciudad son controladas por algunos gobiernos debido a la contaminacin visual que estas generan y sus implicaciones como causales de accidentes por la distraccin de los conductores #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-53.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Pasacalles
            p.mt-4 Medio utilizado para anuncio de nuevos restaurantes, discotecas, espectculos (conciertos, presentaciones artsticas), campaas polticas, entre otros. Se aconseja colocarlos en sitios concurridos por el segmento meta #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-54.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Autoparlante (perifoneo)
            p.mt-4 Vehculos provistos de un parlante y que comnmente se utiliza para anunciar espectculos como conciertos, circos, entre otros #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-55.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Megfono
            p.mt-4 Aparato de transmisin empleado por almacenes para anunciar sus promociones, ayudado con fondo musical para llamar la atencin de la gente que circula en su entorno #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-56.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Publicidad en cine
            p.mt-4 Los asistentes reciben estmulos con mensajes presentados antes del inicio de la pelcula, #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-57.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Carteles
            p.mt-4 Se ubican en las estaciones del metro, estaciones de buses locales e intermunicipales, aeropuertos, al igual que anuncios en tapetes, tableros electrnicos de los estadios #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-58.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 El empaque y la etiqueta del producto
            p.mt-4 Aparte de sus funciones como parte integral del producto, son un excelente medio publicitario, pues el empaque genera imagen y la etiqueta contiene una amplia informacin acerca de las bondades del producto y sus instrucciones de manejo, que son referentes que tienen en cuenta los consumidores para tomar la decisin de comprar #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-59.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Camisetas impresas, cachuchas y las viseras 
            p.mt-4 Son elementos utilizados para hacer publicidad #[strong (Prettel, 2016)].
    .row.mt-3
      .col-10.offset-1.rounded.borde-gris.p-4
        .row
          .col-4.col-lg-4.d-none.d-md-block.px-4.px-lg-5.borde-der-gris
            figure
              img(src="@/assets/template/tema-1-60.png", alt="Texto que describa la imagen")
          .col-12.col-md-8.px-5
            h3 Publicidad mvil
            p.mt-4 En vehculos o en personas resulta til para las compaas por su bajo costo #[strong (Prettel, 2016)].

    .titulo-segundo.mt-5
      #t_1_10.h4 1.10. Campaa
    figure.mt-5
      img(src="@/assets/template/tema-1-61.png", alt="Texto que describa la imagen")
    p.mt-5 Una campaa publicitaria es una serie de anuncios coordinados que comunica un tema razonablemente cohesivo e integrado acerca de una marca. El tema puede estar conformado por varias afirmaciones o puntos, pero debe avanzar sobre un tema esencialmente singular. 
    p Se pueden desarrollar campaas publicitarias exitosas alrededor de un solo anuncio colocado en mltiples medios o pueden estar constituidas de diversos anuncios con un aspecto, sensacin y mensaje similares. Las campaas publicitarias pueden llevarse a cabo durante algunas semanas o por muchos aos. La campaa publicitaria requiere de un sentido agudo de los entornos complejos dentro de los cuales una empresa debe comunicarse con diferentes audiencias #[strong (OGuinn, 2013)].
    p.mt-4 Con base en #[strong Prettel (2016)], los elementos bsicos de una campaa publicitaria son:

    .row.mt-5
      .col-5.mx-3.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-62.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Definicin de la poca
            p.mt-3  La poca es un perodo del ao identificado por una cultura. La poca para la aplicacin en el marketing de las empresas, son ciclos en el ao en que un producto, por la cultura de la poblacin, por sus actividades laborales o escolares, incrementa su demanda.
      .col-5.offset-1.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-63.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Identificacin del tipo de mensaje
            p.mt-3 Un mensaje publicitario que lleva la informacin que se quiere transmitir, cualquiera sea el medio que se utilice, debe ser diseado de tal manera que despierte la atencin del segmento objetivo, e influir de acuerdo con lo que se pretende (informar, persuadir, recordar).
    .row.mt-3
      .col-5.mx-3.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-64.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Cdigo
            p.mt-3  Se debe definir el cdigo o la forma cmo se transmitir el mensaje, es decir si se har en lenguaje verbal o escrito, o en signos, smbolos, que es el lenguaje no lingstico.
      .col-5.offset-1.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-65.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Definicin de medios
            p.mt-3 Los medios publicitarios se deben escoger teniendo en cuenta el tipo de producto, el segmento meta y el presupuesto.
    .row.mt-3
      .col-5.mx-3.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-66.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Establecimiento del momento publicitario, o franja
            p.mt-3  Es el horario en que se requiere pasar el mensaje. Aparentemente el ms indicado es aquel que comprobadamente tenga mayor audiencia.
      .col-5.offset-1.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-67.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Tiempo de duracin del mensaje
            p.mt-3 Tiene gran incidencia en el costo de la campaa, cada segundo tiene un alto valor, dependiendo del medio y el horario que se escoja. Las compaas deben manejar el contenido del mensaje en un lmite de tiempo, a fin de hacerlo menos costoso; cuidndose de que este ahorro no traiga un efecto negativo sobre el objetivo de la campaa.
    .row.mt-3
      .col-5.mx-3.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-68.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Frecuencia del mensaje
            p.mt-3 Es el nmero de mensajes que se pasan en el medio escogido y en una determinada unidad de tiempo. 
      .col-5.offset-1.borde-top-gris.py-4
        .row
          .col-3.d-none.d-md-block
            figure.mt-5(style='margin-left: -40px')
              img(src="@/assets/template/tema-1-69.svg", alt="Texto que describa la imagen")
          .col-12.col-md-9
            h6 Tiempo de duracin de la campaa
            p.mt-3 Es importante escoger la poca en que sea de ms efectividad. Otras compaas, hacen publicidad todo el ao, as les resulte altamente costoso.
    .row.mt-3
      .col-5.mx-3.borde-top-gris.py-4
      .col-5.offset-1.borde-top-gris.py-4
    p.mt-5 #[strong Prettel (2016)], describe el proceso para el desarrollo de una campaa publicitaria, as:
    .row.mt-5
      .col-12.col-md-6.mb-5.mb-sm-0
        ol.lista-ol--cuadro
          li 
            .row.borde-bot.pb-4
              .col-1.text-align-right
                .lista-ol--cuadro__vineta
                  span.text-white 1
              .col-11
                p #[strong Objetivos de la campaa publicitaria:] pueden ser determinantes del tipo de mensaje, de la frecuencia, as como del presupuesto. Si la publicidad es para la demanda primaria, el objetivo sera estimular la demanda de una categora genrica de un producto.
          li
            .row.borde-bot.pb-4
              .col-1.text-align-right
                .lista-ol--cuadro__vineta
                  span.text-white 2
              .col-11
                p #[strong Caractersticas del producto o servicio que se va a publicitar:] en este aspecto se hace referencia a todas las bondades que el bien o servicio ofrece al consumidor y que es justamente la parte central del anuncio publicitario.
          li
            .row.borde-bot.pb-4
              .col-1.text-align-right
                .lista-ol--cuadro__vineta
                  span.text-white 3
              .col-11
                p #[strong Perfil del segmento meta:] los grupos de mercados, de acuerdo con sus caractersticas (edad, gnero, nivel econmico, profesin, entre otras), tienen un comportamiento que los lleva a consumir cierto tipo y cantidad de productos.
          li
            .row.borde-bot.pb-4
              .col-1.text-align-right
                .lista-ol--cuadro__vineta
                  span.text-white 4
              .col-11
                p #[strong La estrategia publicitaria de la competencia:] investigar lo que est haciendo la competencia con sus estrategias publicitarias (medios, tipo de mensaje, objetivo que pretende, segmento al que llega, presupuesto) se ha convertido en un factor competitivo.
          li
            .row.borde-bot.pb-4
              .col-1.text-align-right
                .lista-ol--cuadro__vineta
                  span.text-white 5
              .col-11
                p #[strong Presupuesto publicitario: ] determinar de dnde se obtendr la partida econmica para financiar la campaa.
      .col-6.d-none.d-md-block.align-self-center
        figure
