          figure.mb-2
            img(src="@/assets/template/tema-1-71.png", alt="Texto que describa la imagen")
        .col-12.col-md-8
          p Una vez que la empresa ha encontrado la combinacin de medios ms efectiva para llegar a su pblico objetivo, la siguiente pregunta que tiene que responder es: con qu frecuencia debe repetir el mensaje, para conseguir un determinado nivel de notoriedad? Si la frecuencia es pequea es posible que los mensajes no penetren en la mente del pblico objetivo y que los resultados de notoriedad y comprensin sean bajos. Por otra parte, si el mensaje se repite con demasiada frecuencia, los clientes potenciales podran irritarse, lo que perjudicaran las percepciones de la publicidad, de los productos y de la propia compaa, #[strong (Best, 2007)].
          p.mt-4 Para #[strong Prettel (2016)] la frecuencia debe determinarse de acuerdo con:  
          .row.mt-3
