    p.mt-5 La tica en la publicidad hace parte del comentario diario de miles de personas. Se especula acerca del alcance que estn tomando los diferentes anuncios a travs de los diferentes medios de comunicacin. No cabe duda de que la publicidad incide en los cambios de hbitos de los consumidores y hasta en su estilo de vida. Se especula mucho acerca de lo tico que puede ser, que la publicidad sea un factor de incidencia en los cambios de conducta del consumidor, los gobiernos tratan de colocar algunas barreras a la publicidad con desmedida incidencia en la conducta de los consumidores, y controles para que los publicistas no caigan en dilemas ticos (Prettel, 2016).
    p.mt-4 Por ley, las compaas deben evitar la publicidad falsa o engaosa; adems, los anunciantes no deben hacer declaraciones falsas, como sugerir que un producto cura algo cuando en realidad no lo hace. Tambin deben evitar anuncios que tengan la posibilidad de engaar, incluso cuando nadie resulte realmente engaado (Kotler, 2017).
    p.mt-4 En Colombia, la Superintendencia de Industria y Comercio, en su rol de autoridad nacional de proteccin al consumidor, expidi la Gua de buenas prcticas en la publicidad a travs de influenciadores, una iniciativa que busca proteger a los consumidores en la era digital, luego de evidenciarse el papel que juegan los influenciadores al orientar la toma de decisiones de sus seguidores en las distintas redes sociales frente a cules productos adquirir.
    p.mt-4 Esta nueva forma de hacer publicidad ha venido creciendo sin informacin clara para los consumidores, quienes creen recibir contenidos producto de experiencias personales del influenciador, ignorando que, en ocasiones, los mismos derivan de una relacin contractual con un anunciante, la cual, si bien no necesariamente resta veracidad al mensaje, su omisin resta transparencia frente a la decisin de consumo.
